.select{
    width:500px;
    margin:0 auto;
}
.selectS{
    width:100%;
}
.selectTop{
    margin:0 auto;
    width:80%;
    margin-top:40px;
}
.selectTop li{
    width:100%;
    list-style: none;
    margin-bottom: 10px;
    text-align: center;
}
.clearLeft{
    clear:left;
}
.selectTop button{
    width:90%;
    padding:10px;
    border-radius: 20px;
    color:white;
    background-color: rgb(71, 203, 255);
    cursor: pointer;
}
.selectTop button:hover{
    background-color: rgb(255, 255, 255);
    color:lightskyblue;
    transition: 0.3s;
}
.select h2{
    text-align: center;
    border-bottom: 1px solid;
    margin:0 auto;
    margin-top:30px;
    width:60%;
}
.selectS h2{
    text-align: center;
    border-bottom: 1px solid;
    margin:0 auto;
    margin-top:30px;
    width:70%;
}

.selectBottom{
    width:60%;
    margin:0 auto;
    margin-top:20px;
    text-align: center;
}
.selectBottom button{
    width:95%;
    padding:5px;
    background-color: rgb(240, 254, 255);
    color:rgb(0, 0, 37);
    border:1px solid;
    margin-bottom:10px;
    cursor: pointer;
    font-size: 18px;
    border-radius: 10px;
}
.selectBottom button:hover{
    background-color: white;
    width:100%;
    transition: 0.3s;
}
.serch{
    width:30%;
    margin:0 auto;
    text-align: center;
}
.serch input{
    width:100%;
    font-size: 18px;
    padding: 1px 5px;
}
.serch button{
    width:50%;
    padding: 2px;
    background-color: aliceblue;
    color:rgb(0, 88, 88);
    border: 1px solid;
}


.requestTop{
    margin:0 auto;
    width:60%;
    margin-top:40px;
    padding-bottom:50px;
}
.requestTop button{
    width:100%;
    padding:10px;
    border-radius: 20px;
    color:white;
    background-color: rgb(71, 203, 255);
    cursor: pointer;
}

.requestBottom{
    width:60%;
    margin:0 auto;
    margin-top:20px;
    text-align: center;
}

.requestButton{
    width:70%;
    padding:5px;
    background-color: rgb(240, 254, 255);
    color:rgb(0, 0, 37);
    border:1px solid;
    margin-bottom:10px;
    cursor: pointer;
    font-size: 18px;
    border-radius: 5px;
}
.requestright1{
    width:10%;
    height:30px;
    border-radius: 30px;
    background-color: cornflowerblue;

}
.requestright2{
    width:10%;
    height:30px;
    border-radius: 30px;
    background-color: rgb(220, 173, 20);

}

.alert{
    text-align: center;
    color:darkblue;
    padding:20px 0px;
}
.search {
    width:60%;
    margin:0 auto;
}
.search p{
    text-align: center;
    font-size: 18px;
    padding:10px 0px 3px 0px;
    color: rgb(3, 119, 121);
}
.search input{
    width:100%;
    font-size: 18px;
    padding:5px 1px;
    border-radius: 5px;
}
.searchButton{
    display: block;
    width:100px;
    margin:0 auto;
    margin-top:5px;
    padding:5px 0px;
    border-radius: 10px;
    background-color: rgb(71, 203, 255);
    color:white;
    cursor: pointer;
}
.sendButton{
    display: block;
    width:90%;
    padding:5px;
    margin:0 auto;
    background-color: rgb(240, 254, 255);
    color:rgb(0, 0, 37);
    border:1px solid;
    margin-bottom:10px;
    cursor: pointer;
    font-size: 18px;
    border-radius: 5px;
}
.sendButton:hover{
    width:100%;
    transition: 0.3s;
    background-color: white;
}

