.container{
    width:100%;
    margin:0 auto;
    margin-top:20px;
}
.username{
    width:100%;
    position: fixed;
    top:0px;
}
.container h2{
    text-align: center;
    font-size: 25px;
    margin-bottom: 30px;
    border-bottom:1px solid;
    padding:10px 0px;
    background-color: rgb(255, 255, 255);
}
.chatarea{
    width:500px;
    margin:0 auto;
    margin-top: 80px;
    margin-bottom: 170px;
   
    
}
.chatareaS{
    width:98%;
    margin:0 auto;
    margin-top: 80px;
    margin-bottom: 170px;
   
    
}
.comment1{
    margin-bottom:20px;
    text-align: right;
    
}
.comment2{
    margin-bottom:20px;
    text-align: left;
}
.comment1 p{
    text-align: left;
    border:1px solid;
    background-color: rgba(104, 213, 247, 0.384);
    width:auto;
    max-width: 90%;
    border-radius: 30px;
    padding:5px 15px;
    float:right;
}
.comment2 p{
    text-align: left;
    border:1px solid;
    background-color: rgba(235, 248, 252, 0.384);
    width:auto;
    max-width: 90%;
    border-radius: 30px;
    padding:5px 15px;
    float:left;
}
.clearLeft{
    clear:left;
}
.clearRight{
    clear:right;
}
.form textarea{
    display: block;
    width:500px;
    min-width: 500px;
    margin: 0 auto;
    max-height:50px;
    min-height: 50px;
    font-size:20px;
    padding-top:5px;
    padding-bottom:5px;
    padding-right: 50px;
    border-radius: 10px;
    max-width: 98%;
}
.formS textarea{
    display: block;
    width:98%;
    min-width: 98%;
    margin: 0 auto;
    font-size:20px;
    min-height: 50px;
    max-height:50px;
    padding-top:5px;
    padding-bottom:5px;
    padding-right: 50px;
    border-radius: 10px;
    max-width: 98%;
}
.form{
    position: fixed;
    background-color: rgb(177, 255, 255);
    bottom: -5px;
    width:100%;
    border-top: 2px solid;
    padding-top: 10px;
    height:80px;
}
.formS{
    position: fixed;
    background-color: rgb(177, 255, 255);
    bottom: -5px;
    width:100%;
    border-top: 2px solid;
    padding-top: 10px;
    height:80px;
}
.pointerS{
    position: relative;
    width:100%;
    margin:0 auto;
}
.pointer{
    position: relative;
    width:500px;
    margin:0 auto;
}
.form button{
    position:absolute;
    left:440px;
    top:5px;
    z-index: 8;
    width:50px;
    height:40px;
    border-radius: 50px;
    background-color: rgba(0, 119, 255, 0.548);
    color:white;
    font-size: 20px;

}
.formS button{
    position:absolute;
    right:2%;
    top:5px;
    z-index: 8;
    width:50px;
    height:40px;
    border-radius: 50px;
    background-color: rgba(0, 119, 255, 0.548);
    color:white;
    font-size: 20px;

}
.leave{
    position:fixed;
    top:10px;
    left:5px;
    width:150px;
    border-radius: 10px;
    padding:5px;
    background-color: ghostwhite;
    cursor: pointer;
}
.leaveS{
    position:fixed;
    top:10px;
    left:5px;
    width:50px;
    border-radius: 10px;
    padding:5px;
    background-color: ghostwhite;
    cursor: pointer;
}
.leave:hover{
    width:210px;
    transition: 0.3s;
    background-color: white;
}
.chatarea{
    min-height:400px;
}