*{
    box-sizing:border-box;
    margin:0px;
    padding:0px;
    
}
.clear{
    clear:left;
    clear:right;
}
.container{
    padding:40px 0px;
}
.home{
    width:500px;
    margin:0 auto;
    margin-top:150px;
    text-align: center;
    background-color: rgba(0, 217, 255, 0.39);
    border-radius: 50px;
}
.homeS{
    width:90%;
    margin:0 auto;
    margin-top:150px;
    text-align: center;
    background-color: rgba(0, 217, 255, 0.39);
    border-radius: 50px;
}
.homeSign{
    width:500px;
    margin:0 auto;
    margin-top:10px;
    text-align: center;
    background-color: rgba(0, 217, 255, 0.39);
    border-radius: 50px;
}
.homeSignS{
    width:90%;
    margin:0 auto;
    margin-top:10px;
    text-align: center;
    background-color: rgba(0, 217, 255, 0.39);
    border-radius: 50px;
}
.homeLoginS{
    width:90%;
    margin:0 auto;
    margin-top:150px;
    text-align: center;
    background-color: rgba(0, 217, 255, 0.39);
    border-radius: 50px;
}
.homebutton{
    color:rgb(0, 46, 107);
    background-color: rgb(255, 255, 255);
    padding:10px;
    width:60%;
    margin-top:10px;
    margin-bottom:10px;
    border-radius: 30px;
    cursor: pointer;
}
.homebutton:hover{
    background-color:rgb(136, 237, 255);
    color:white;
    font-size: 15px;
    transition: 0.2s;
}
.home h3{
    color:rgb(255, 255, 255);
    text-shadow: 1px 1px 10px rgb(0, 110, 255);
    padding-top: 40px;
}

.homeS h3{
    color:rgb(255, 255, 255);
    text-shadow: 1px 1px 10px rgb(0, 110, 255);
    padding-top: 10px;
    padding-bottom: 20px;
}

.create-container{
    padding-top:30px;
    padding-bottom:30px;
}
.create-container button{
    width:60%;
    margin-top:30px;
    background-color: rgb(255, 255, 255);
    padding:10px;
    border-radius: 30px;
}
.create-container input{
    width:60%;
    font-size: 18px;
    padding:5px;
}
.version{
    text-align: center;
}