}
.pass2{
    font-size: 14px;
}
.container button{
    color:rgb(0, 0, 0);
    background-color: rgb(255, 255, 255);
    padding:10px;
    width:65%;
    margin-top:10px;
    margin-bottom:30px;
    border-radius: 30px;
}
.containerS button{
    color:rgb(0, 0, 0);
    background-color: rgb(255, 255, 255);
