.errorTop{
    padding:20px 0px;
    display: block;
    margin:0 auto;
    text-align: center;
    color:rgb(255, 96, 4);
}
.form{
    position: relative;
}
.form p{
    position:absolute;
    top:70px;
    left:100px;
    color:rgb(255, 96, 4);
}
.formS{
    position: relative;
}
.formS p{
    position:absolute;
    top:70px;
    left:30px;
    color:rgb(255, 96, 4);
